button.loanButton {
  display: block;
  font-weight: bold;
  max-width: 320px;
  box-shadow: 0.1em 0.1em 0.3em rgba(4, 4, 4, 0.3);
  padding: 0.5em 1em;
  cursor: pointer;
  transition: all 0.2s;
  width: 100%;
}
button.loanButton_fullcolor {
  border: 0.1em solid #dfa700;
  color: #ffffff;
  background: linear-gradient(#ffd174, #fec601 45%, #dfa700 55%, #fec601);
}
button.loanButton_fullcolor:hover:not(:disabled) {
  background: linear-gradient(#ffc041, #ffc041 45%, #ac8100 55%, #cb9e01);
}
button.loanButton_square {
  border-radius: 0;
}
button.loanButton_rounded {
  border-radius: 6px;
}
button.loanButton_oval {
  border-radius: 2em;
}